<template>
  <div class="index-wrap">
    <van-nav-bar
      title="我的卖单"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <form action="/">
      <van-search
        v-model="value"
        show-action
        placeholder="请输入搜索关键词"
        shape="round"
        @search="onSearch"
        @cancel="onCancel"
      />
    </form>
    <div class="more-search">
      高级搜索
      <van-icon name="arrow" />
    </div>
    <van-tabs v-model="active">
      <van-tab title="待审核"></van-tab>
      <van-tab title="交易中"></van-tab>
      <van-tab title="纠纷中"></van-tab>
      <van-tab title="已关闭"></van-tab>
      <van-tab title="已完成"></van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  name: 'orders',
  data () {
    return {
      value: '',
      active: 0,
      list: []
    }
  },
  computed: {
  },
  created () {
    this.initList()
  },
  methods: {
    onClickLeft () {
      this.$router.push({ path: '/user' })
    },
    initList () {
      let list = [
        { 
          title: '25收百果园30代金券',
          desc:'银联*平安*光大*平安RUN、每日10点光大银行15买30平安RUN、每日10点光大银行15买30平安RUN、每日10点光大银行15买30', 
          id: 1,
          price: 25,
          star: true
        },
        { 
          title: '25收百果园30代金券',
          desc: '银联*平安*光大*平安RUN', 
          price: 25,
          id: 2 
        },
        { 
          title: '25收百果园30代金券',
          desc:'银联*平安*光大', 
          price: 25,
          id: 3 
        },
        { title: '25收百果园30代金券',
          desc:'银联*平安*光大*平安RUN、每日10点光大银行15买30', 
          price: 25,
          id: 4
        },
        { title: '25收百果园30代金券',
          desc:'银联*平安*光大*平安RUN、每日10点光大银行15买30', 
          price: 25,
          id: 5
        }
      ]

      this.list = list

      for (let i = 0; i < list.length; i++) {
        const el = list[i]

        if (el.desc.length >= 20) {
          this.spreadArray[i] = true
        } else {
          this.spreadArray[i] = false
        }
      }
    },
    onSearch(val) {
      this.$toast(val)
    },
    onCancel() {
      this.$toast('取消')
    }
  }
}
</script>

<style scope lang='less'>
  .more-search {
    text-align: right;
    font-size: 14px;
    color: #666;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 5px;
  }
</style>



